import React from 'react';
import classesHeader from './Header.module.css';
import { NavLink } from 'react-router-dom';
import logo from "../../assets/images/logo.ico";

const Header = (props) => {
 const click = ()=>{
    props.logout();
 }
    return (
        <header className={classesHeader.Header}>
            <img className={classesHeader.Header__img} src={logo}></img>
            {/* <textarea className={classesHeader.search} name="" id="" placeholder=''></textarea> */}
            <NavLink  className={classesHeader.btn} to="/FindUsers">  </NavLink>
            <NavLink  className={classesHeader.btn} to="/communities">  </NavLink>
            <NavLink  className={classesHeader.btn} to="/games">  </NavLink>
            <NavLink  className={classesHeader.btn} to="/music">  </NavLink>
            <NavLink  className={classesHeader.btn} to="/help">  </NavLink>

                {props.isAuth
                    ? <> 
                        <div className={classesHeader.btn}> {props.login} </div>
                        <div className={classesHeader.btn} onClick={click}> </div>
                    </>
                    : <NavLink to={'/login'} className={classesHeader.btn}>  </NavLink>}

        </header>
    );
}

export default Header;


